<template>
    <div id="app">
        <v-app>
            <v-navigation-drawer clipped v-model="drawer" class="hidden-md-and-up" disable-resize-watcher app>
                <v-list-item>
                    <v-list-item-avatar>
                        <v-img class="mx-2" src="../src/assets/logo.svg" max-height="34" max-width="34" contain></v-img>
                    </v-list-item-avatar>
                    <v-list-item-content>
                        <v-list-item-title class="text-h5 font-weight-black">Jftrns</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
                <v-divider></v-divider>
                <v-list shaped>
                    <v-list-item-group>
                        <v-list-item :to="item.path" v-for="(item, drawer) in items" :key="drawer" color="primary" @click="toTop()">
                            <v-list-item-icon>
                                <v-icon v-text="item.icon"></v-icon>
                            </v-list-item-icon>
                            <v-list-item-content>
                                <v-list-item-title v-text="item.text" class="font-weight-medium"></v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                    </v-list-item-group>
                </v-list>
            </v-navigation-drawer>
            <v-app-bar
                fixed
                clipped-left
                flat
                :color="appBarBackground"
                v-bind:style="[isAppBarCollapsed ? {'border-bottom-color' : 'var(--v-divider-base) !important'} : {'border-bottom-color' : 'rgba(0, 0, 0, 0) !important'}]"
                v-scroll="onScroll"
                style="border-width: 0 0 thin; border-style: solid;"
            >
                <v-app-bar-nav-icon @click="drawer = !drawer" class="hidden-md-and-up" color="primary"></v-app-bar-nav-icon>
                <v-spacer>
                    <router-link to="/" style="text-decoration: none;">
                        <v-toolbar-title class="flex text-center text-h5 font-weight-black" @click="toTop()">Jftrns</v-toolbar-title>
                    </router-link>
                </v-spacer>
                <div class="red flex-grow-1 hidden-sm-and-down"></div>
                <v-list class="hidden-sm-and-down" style="background-color: transparent;">
                    <v-list-item-group>
                        <v-btn color="primary" rounded small :to="item.path" v-for="(item, navigation) in items" :key="navigation" v-text="item.text" text class="mx-1" @click="toTop()"></v-btn>
                    </v-list-item-group>
                </v-list>
                <v-btn color="transparent" icon class="hidden-md-and-up">
                    <v-icon></v-icon>
                </v-btn>
            </v-app-bar>
            <v-main>
                <v-scroll-y-transition hide-on-leave>
                    <router-view></router-view>
                </v-scroll-y-transition>
                <v-footer padless>
                    <v-card flat tile width="100%" class="text-center">
                        <v-card-text class="primary--text text-overline text-decoration-underline font-weight-black">jftrns</v-card-text>
                        <v-card-text>
                            <v-btn v-for="icon in icons" :key="icon.icon" class="mx-4 primary--text" icon :href="icon.url" style="background-color: var(--v-onPrimaryShade-base);">
                                <v-icon size="24px" color="primary">
                                    {{ icon.icon }}
                                </v-icon>
                            </v-btn>
                        </v-card-text>
                        <v-card-text class="primary--text">© Jeffrey Turns {{ new Date().getFullYear() }}</v-card-text>
                    </v-card>
                </v-footer>
                <transition name="fab-transition">
                    <v-btn fab small fixed right bottom v-scroll="onScroll" v-show="scroll_fab" @click="toTop()">
                        <v-icon color="primary">mdi-chevron-up</v-icon>
                    </v-btn>
                </transition>
            </v-main>
        </v-app>
    </div>
</template>

<style scoped>
    @import "../src/assets/styles/main.css";
</style>

<script>
    let config = require("./assets/config.json");
    let site = config.path;
    import routesJson from "../src/router/routes_nav.json";

    export default {
        data: () => ({
            appBarBackground: "transparent",
            drawer: false,
            isAppBarCollapsed: false,
            scroll_fab: false,
            fav: true,
            menu: false,
            message: false,
            items: routesJson.routes,
            icons: [],
            namePrj: 'undef'
        }),
        watch: {
            $route(to) {
                document.title = to.meta.title + " | Jftrns" || "Jftrns";
            },
        },
        computed: {},
        created() {
            fetch(site + "footer" + ".json")
                .then((response) => response.json())
                .then((data) => (this.icons = data.data))
                .catch((err) => {
                    console.log(err);
                });
        },
        methods: {
            onScroll(e) {
                if (typeof window === "undefined") return;
                const top = window.pageYOffset || e.target.scrollTop || 0;
                if ((this.scroll_fab = top > 200)) {
                    this.appBarBackground = "background";
                    this.isAppBarCollapsed = true;
                } else {
                    this.isAppBarCollapsed = false;
                    this.appBarBackground = "transparent";
                }
            },
            toTop() {
                this.$vuetify.goTo(0);
            },
            goBack() {
                this.$router.go(-1);
                this.$vuetify.goTo(0);
            },
            upperCaseFirstLetter(string) {
                return string.charAt(0).toUpperCase() + string.slice(1);
            },
        },
    };
</script>
