<template>
    <v-app>
        <div class="header" v-if="!isFailedLoad">
            <v-layout primary--text align-center column justify-center style="margin-top: 25vh;">
                <div class="text-h3 text-center font-weight-black" data-aos="fade-in" data-aos-duration="1000">{{ items.title }}</div>
            </v-layout>
        </div>
        <v-card flat class="pa-10" v-for="item in items.sections" :key="item.title">
            <v-card-text class="primary--text text-h4 font-weight-medium"> {{ items.title }}</v-card-text>
            <v-card-text class="primary--text text-body-1"> {{ item.description }} </v-card-text>
            <v-layout primary--text align-center column justify-center>
                <v-img :src="site + item.mockup" lazy-src="../assets/images/placeholder.svg" width="1064" :aspect-ratio="12/9" lign-center class="ma-16 rounded-lg"></v-img>
            </v-layout>
            <v-divider></v-divider>
        </v-card>
        <div class="errHeader" v-if="isFailedLoad">
            <v-container style="margin-top: 25vh;">
                <v-row class="mb-6">
                    <v-col>
                        <v-img class="ma-8" max-height="300" contain src="../assets/error_illust.svg" />
                    </v-col>
                    <v-col>
                        <v-container bg fill-height grid-list-md text-xs-center>
                            <v-layout row wrap align-center>
                                <v-flex>
                                    <div class="text-h5 text--primary text-center pb-2 font-weight-bold">Something went wrong</div>
                                    <div class="text-subtitle-1 text--primary  text-center pb-4 font-weight-medium">This project page does not exsist</div>
                                    <v-layout justify-center>
                                         <v-btn rounded outlined color="primary" class="mt-4 me-2" @click="goBack()" @click.native="toTop()">Go back</v-btn>
                                    <v-btn text rounded color="primary" class="mt-4 ms-2" @click="snackbar = true">Error message</v-btn>
                                    </v-layout>
                                </v-flex>
                            </v-layout>
                        </v-container>
                    </v-col>
                </v-row>
            </v-container>
            <v-snackbar color="white text--primary" v-model="snackbar">
                {{ textError }}
                <template v-slot:action="{ attrs }">
                    <v-btn color="primary" text rounded v-bind="attrs" @click="snackbar = false">
                        Close
                    </v-btn>
                </template>
            </v-snackbar>
        </div>
    </v-app>
</template>

<style scoped>
    .header {
        background-image: url("../assets/images/project.svg");
        height: 50vh;
        background-attachment: fixed;
        background-size: cover;
        position: relative;
    }
    .errHeader {
        height: 100vh;
        background-attachment: fixed;
        background-size: cover;
        position: relative;
    }
</style>

<script>
    let config = require("../assets/config.json");
    let site = config.path;

    export default {
        data() {
            return {
                items: [],
                site: config.path,
                isFailedLoad: false,
                textError: "",
                snackbar: false,
            };
        },
        created() {
            fetch(site + "/projects/" + this.$route.params.project.toString() + ".json")
                .then((response) => response.json())
                .then((data) => (this.items = data))
                .catch((err) => {
                    this.textError = err;
                    console.log(err);
                    this.isFailedLoad = true;
                });
            document.title = this.upperCaseFirstLetter(this.$route.params.project.toString()) + " | Jftrns" || "Jftrns";
        },
        methods: {
            upperCaseFirstLetter(string) {
                return string.charAt(0).toUpperCase() + string.slice(1);
            },
            goBack() {
                this.$router.go(-1);
            },
            toTop() {
                this.$vuetify.goTo(0);
            }
        },
    };
</script>
